.hero-section {
    width: 100%;
    display: flex;
    padding-top: 20px;

    .hero-left h2{
        width: 64%;
    }

    .hero-right {
        display: flex;
        width: 25%;
        align-items: center;
        gap: 14px;
        justify-content: center;
    }

    .filter-option {
        display: flex;
        gap: 10px;
        height: 32px;
        width: 128px;
        background-color: $tertiary-color;
        justify-content: center;
        align-items: center;
    }
}
