.nav-bar {
    padding: 20px 0px 20px 0px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .nav-links {
        display: flex;
        gap: 15px;
    }

    .nav-links a {
        color: $nav-color;
    }

    #blog a {
        color: $ashy-color;
    }
    
}